// Video

.video {
  position: relative;
  border: 1px solid $basic-semi-gray;

  @include tablet {
    order: -1;
  }

  @include desktop {
    min-width: 682px;
    grid-row: 1/3;
  }
}

// Button

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  width: 101px;
  height: 76px;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 0.3s ease-in-out;
  border: none;
  background-color: transparent;
  cursor: pointer;

  @media (any-hover: hover) {
    &:hover {
      transform: translate(-50%, -50%) scale(1.2);
      transition: transform 0.25s ease-in-out;
    }
  }

  &:focus-visible {
    outline: 2px solid $basic-lilac;
  }

  &:active {
    transform: translate(-50%, -50%) scale(0.8);
    transition: transform 0.25s ease-in-out;
    opacity: 0.3;
  }
}

// Link

.video__link {
  &:focus-visible {
    outline-color: $basic-lilac;
    outline-offset: 1px;
  }
}

.video__button svg {
  width: 101px;

  @media (any-hover: hover) {
    width: 121px;
  }
}

.video__image {
  width: 100%;
}
